<template>
  <div class="chart4" ref="chart4"></div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'chart4',

  data() {
    return {
      myChart: null,
      seriesData: [
        {name: '', value: '166', obj: {people: '25852345',gdp: '15983.65'}},
        {name: '', value: '122.8', obj: {people: '3648100',gdp: '2080.17'}},
        {name: '', value: '118.3', obj: {people: '24049155',gdp: '20514.20'}},
        {name: '', value: '72.23', obj: {people: '5923957',gdp: '3346.63'}},
        {name: '', value: '48.14', obj: {people: '83674366',gdp: '53850.79'}},
        {name: '', value: '45.48', obj: {people: '31350088',gdp: '14879.20'}},
        {name: '', value: '45.44', obj: {people: '25019831',gdp: '10243.30'}},
        {name: '', value: '38.33', obj: {people: '47209277',gdp: '27146.76'}},
        {name: '', value: '23.6', obj: {people: '50126804',gdp: '24740.86'}},
        {name: '', value: '21.18', obj: {people: '66444864',gdp: '46063.09'}},
        {name: '', value: '20.56', obj: {people: '39528999',gdp: '29800.98'}},
        {name: '', value: '18.77', obj: {people: '74610235',gdp: '40391.30'}},
        {name: '', value: '18.74', obj: {people: '24073453',gdp: '13235.52'}},
        {name: '', value: '18.59', obj: {people: '57752557',gdp: '50012.94'}},
        {name: '', value: '18', obj: {people: '126012510',gdp: '124369.67'}},
        {name: '', value: '17.6', obj: {people: '38562148',gdp: '19586.42'}},
        {name: '', value: '16.7', obj: {people: '45188635',gdp: '29619.70'}},
        {name: '', value: '16.7', obj: {people: '99365519',gdp: '58887.41'}},
        {name: '', value: '15.63', obj: {people: '34915616',gdp: '22590.16'}},
        {name: '', value: '15.38', obj: {people: '101527453',gdp: '83095.90'}},
        {name: '', value: '14.59', obj: {people: '42591407',gdp: '27584.10'}},
        {name: '', value: '13.97', obj: {people: '61027171',gdp: '42959.20'}},
        {name: '', value: '12.13', obj: {people: '41540086',gdp: '48810.36'}},
        {name: '', value: '10.26', obj: {people: '84748016',gdp: '116364.20'}},
        {name: '', value: '10.2', obj: {people: '64567588',gdp: '73515.76'}},
        {name: '', value: '8.23', obj: {people: '32054159',gdp: '27894.02'}},
        {name: '', value: '6.64', obj: {people: '7202654',gdp: '4522.31'}},
        {name: '', value: '3.6', obj: {people: '23875314',gdp: '49985.81'}},
        {name: '', value: '3.4', obj: {people: '10081232',gdp: '6475.20'}},
        {name: '', value: '1.68', obj: {people: '21893095',gdp: '40269.60'}},
        {name: '', value: '1.13', obj: {people: '13866009',gdp: '15695.05'}},
        {name: '', value: '0.63', obj: {people: '24870895',gdp: '43214.85'}},
        {name: '', value: '0.11', obj: {people: '7589499',gdp: '23748.97'}},
        {name: '', value: '0.0025', obj: {people: '683218',gdp: '1929.27'}},
      ],
      timer: null,
      historyList: [],
      curIndex: -1
    };
  },

  mounted() {
    this.$refs.chart4.oncontextmenu = ()=>false
    this.initMapEcharts('china');
  },

  methods: {
    initMapEcharts(name){
      axios.get(`/json/${name}.json`).then(res=>{
        this.$echarts.registerMap(name,res.data)
        this.$nextTick(()=>{
          this.myChart&&this.myChart.dispose()
          this.myChart = this.$echarts.init(this.$refs.chart4)
          // 
          const option = {
            // 
            title: {
              text:"",
            },
            // 
            tooltip: {
              trigger: 'item',
              textStyle: {
                align: 'left'
              },
              formatter: params=> {
                const item = this.seriesData.find(item=>item.name===params.name)
                if(item){
                  return `${params.name}<br>
                  ${params.value+ ' ' || 0}<br>
                  ${item.obj.people+ ' ' || 0}<br>
                  GDP${item.obj.gdp+ ' ' || 0}`
                }else{
                  return `${params.name}`
                }
              }
            },
            // 
            visualMap: {
              min: 0,
              max: 200,
              text: ['High', 'Low'],
              itemHeight: '50',
              realtime: false,
              calculable: true,
              inRange: {
                  color: ['lightskyblue', 'yellow', 'orangered']
              }
            },
            // 
            series: [
              {
                type: 'map',
                map: name,
                roam: true,
                // 
                data: this.seriesData,
              },
              
            ]
          }
          this.myChart.setOption(option)
          // 
          this.myChart.on('click', (params)=> {
            const name = params.name
            let topName = this.myChart.getOption().series[0].map
            this.historyList.push(topName)
            this.initMapEcharts(name);
          })
          // 
          this.myChart.on("contextmenu", () => {
            if(this.historyList.length){
              const name = this.historyList.pop()
              this.initMapEcharts(name);
            }
          });
          // tooltip
          this.setTimer()
          // tooltip
          this.myChart.on('mouseover', ()=> {
            clearInterval(this.timer)
          })
          // tooltip
          this.myChart.on('mouseout', ()=> {
            this.setTimer()
          })
        })
      }).catch(err=>{
        console.log(err)
      })
    },

    setTimer() {
      // 
      this.timer && clearInterval(this.timer)
      this.timer = setInterval(() => {
        const len = this.seriesData.length;
        // dispatchActionecharts
        this.myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
          dataIndex: this.curIndex
        })
        // 
        this.curIndex = (this.curIndex + 1) % len;
        // 
        this.myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: this.curIndex
        })
        // tooltip
        this.myChart.dispatchAction({
          type: 'showTip',
          seriesIndex:0,
          dataIndex: this.curIndex
        })
      }, 2100)
    },

    resizeFun(){
      this.myChart.resize()
    }
  },
};
</script>

<style lang="scss" scoped>
.chart4{
  width: 100%;
  height: 100%;
}
</style>